import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { FacturacionService } from '../services/facturacion.service';
import { environment } from 'environments/environment';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { Subject, takeUntil } from 'rxjs';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatDialog } from '@angular/material/dialog';
import { DialogAccionesComponent } from './dialog-acciones-factura/dialog-acciones/dialog-acciones.component';
import { DomSanitizer } from '@angular/platform-browser';
import { AsociarVentaComponent } from './asociar-venta/asociar-venta/asociar-venta.component';

@Component({
    selector: 'app-facturas',
    templateUrl: './facturas.component.html',
    styleUrls: ['./facturas.component.scss'],
})
export class FacturasComponent implements OnInit {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    column_params = [
        'fechaEmision',
        'tipo',
        'num_comprobante',
        'puntoVenta',
        'cliente_razonsocial',
        /* 'formaPago', */
        'importeTotal',
        'generada',
        'acciones',
    ];
    headers = [
        'Fecha',
        'Tipo',
        'Nmero',
        'Pto. de Venta',
        'Cliente',
        /* 'Forma de Pago', */
        'Total',
        'Generada',
        '',
    ];
    funcionesObjeto = null;
    accionesObjeto = null;
    showMenu: any;
    filtroBusqueda: string = '';
    filtersLike = ['descripcion'];
    env = environment.ventas.view_fe_facturas + '?';
    extraParams = '';
    base64: string;
    safeURL = null;
    constructor(
        private headerTextService: HeaderTextService,
        private router: Router,
        private facturacionService: FacturacionService,
        private empresaService: EmpresaService,
        private alert: AppAlertService,
        private dialog: MatDialog,
        private sanitizer: DomSanitizer
    ) {
        this.headerTextService.setTitulo(this.router);
    }
    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        this.empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        this.funcionesObjeto = [
            {
                iconoAccion: () => '',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
                visible: () => false,
            },
        ];

        this.showMenu = () => true;

        this.accionesObjeto = [
            {
                    nombre_boton: "Editar",
                    functionName: 'editar',
                    iconoAccion: (factura) => 
                    factura.generada == 'Si'
                        ? 'jedstion:editar_disabled'
                        : 'jedstion:editar',
                    iconoAccionado: () => '',
                    iconoDeshabilitado: () => '',
                    visible: () => true
                },
            {
                nombre_boton: 'Imprimir Factura',
                functionName: 'imprimir_factura',
                iconoAccion: () => 'jedstion:imprimir',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
                visible: () => true,
            },
            {
                nombre_boton: 'Asociar Cobro',
                functionName: 'asociar_cobro',
                iconoAccion: (factura) =>
                    factura.idcobro
                        ? 'jedstion:cotizar_disabled'
                        : 'jedstion:cotizar',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
                visible: () => true,
            },
            {
                nombre_boton: 'Generar Factura',
                functionName: 'generar_factura',
                iconoAccion: (factura) =>
                    factura.generada == 'Si'
                        ? 'jedstion:generar_factura_disabled'
                        : 'jedstion:generar_factura',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
                visible: () => true,
            },
            {
                nombre_boton: 'Eliminar',
                functionName: 'eliminar',
                iconoAccion: (factura) =>
                    factura.generada == 'Si'
                        ? 'jedstion:eliminar_disabled'
                        : 'jedstion:eliminar',
                iconoAccionado: () => '',
                iconoDeshabilitado: () => '',
                visible: () => true,
            },
        ];
    }

    generarAcciones(event: { name: string; params: any }) {
        switch (event.name) {
            case 'editar':
                if (event.params.data.generada == 'Si') {
                    this.alert.error('La factura fue generada y no puede editarse');
                } else {
                    this.editarFactura(event);
                }
                break;
            case 'imprimir_factura':
                this.imprimirFactura(event);
                break;
            case 'asociar_cobro':
                if (event.params.data.idcobro) {
                    this.alert.error('La factura ya tiene un cobro asociado');
                } else {
                    this.asociarCobro(event);
                }
                break;
            case 'generar_factura':
                if (event.params.data.generada == 'Si') {
                    this.alert.error('La factura ya fue generada');
                } else {
                    this.generarFactura(event);
                }
                break;
            case 'eliminar':
                if (event.params.data.generada == 'Si') {
                    this.alert.error('No puede eliminar una factura generada');
                } else {
                    this.deleteFactura(event);
                }
                break;
        }
    }

    nuevaFactura() {
        this.router.navigate(['facturas/nueva-factura']);
    }

    editarFactura(event){
        let codigoFactura = event.params.data.codigo;
        this.router.navigate([`facturas/editar-factura/${codigoFactura}`])
    }

    deleteFactura(event) {
        let codigoFactura = event.params.data.codigo;
        this.dialog
            .open(DialogAccionesComponent, {
                panelClass: 'ayuda-dialog',
                data: {
                    codigoFactura: codigoFactura,
                    title: 'Eliminar Factura',
                    accion: 'delete',
                    descripcion: `<span class="font-semibold text-lg">Confirma la eliminacin de la factura N ${codigoFactura}? <br>
                    Esta operacin es irreversible. Desea continuar?</span>`,
                },
            })
            .afterClosed()
            .subscribe({
                next: (res) => {
                    this.tabla.eventoUpdate();
                },
            });
    }

    generarFactura(event) {
        let codigoFactura = event.params.data.codigo;
        this.dialog
            .open(DialogAccionesComponent, {
                panelClass: 'ayuda-dialog',
                data: {
                    codigoFactura: codigoFactura,
                    title: 'Generar Factura',
                    accion: 'generar',
                    descripcion: `<span class="font-semibold text-lg">Confirma la generacin de la factura N ${codigoFactura}? <br>
                    Esta operacin es irreversible. Desea continuar?</span>`,
                },
            })
            .afterClosed()
            .subscribe({
                next: (res) => {
                    this.tabla.eventoUpdate();
                },
            });
    }

    asociarCobro(event) {
        let data = event.params.data;
        this.dialog
            .open(AsociarVentaComponent, {
                data: data,
                panelClass: 'ayuda-dialog',
            })
            .afterClosed()
            .subscribe({
                next: (res: any) => {
                    this.tabla.eventoUpdate();
                },
            });
    }

    imprimirFactura(event) {
        let data = event.params.data;
        let filter = `?id=${data.codigo}&copia=$1`;
        this.facturacionService.imprimirFactura(filter).subscribe({
            next: (res: any) => {
                this.base64 = 'data:application/pdf;base64,' + res.result;
                this.safeURL = this.sanitizer.bypassSecurityTrustResourceUrl(
                    this.base64
                );
            },
        });
    }

    cerrarIFrame() {
        this.safeURL = null;
    }
}
