import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { FacturacionService } from '../../../services/facturacion.service';
import { AppAlertService } from '@shared/service/app/alert.service';
import { Subject, takeUntil } from 'rxjs';

@Component({
    selector: 'app-dialog-acciones',
    templateUrl: './dialog-acciones.component.html',
    styleUrls: ['./dialog-acciones.component.scss'],
})
export class DialogAccionesComponent {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    deposito_default: number;
    showAlert: boolean;
    _alertConfig: any;
    idFactura: number;
    accion: string;
    descripcion: string;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        private alert: AppAlertService,
        private facturacionService: FacturacionService,
        public dialogRef: MatDialogRef<DialogAccionesComponent>
    ) {
        this.title = this.data.title;
        this.descripcion = this.data.descripcion;
        this.idFactura = this.data.codigoFactura;
        this.accion = this.data.accion;

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.deposito_default = empresa.deposito_default;
            });
    }

    ngOnInit(): void {
        // Load empresa data
        this._empresaService.getEmpresa();
    }

    onCloseMenu(data) {
        this.dialogRef.close(data);
    }

    onSubmit() {
        switch (this.accion) {
            case 'delete':
                this.facturacionService
                    .deleteFactura(this.idFactura)
                    .subscribe({
                        next: (res: any) => {
                            if (res.success = true) {
                                this.alert.success(res.message);
                                this.onCloseMenu(true);
                            } else {
                                this.alert.error(res.errors);
                            }
                        },
                    });
                break;
            case 'generar':
                this.facturacionService
                    .generarFactura(this.idFactura)
                    .subscribe({
                        next: (res: any) => {
                            if (res.success == true) {
                                this.alert.success(res.message);
                                this.onCloseMenu(true);
                            } else {
                                this.alert.error(res.message);
                            }
                        },
                    });
                break;
        }
    }
}
