import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { EmpresaService } from '@shared/service/app/empresa.service';
import {
    debounceTime,
    distinctUntilChanged,
    startWith,
    Subject,
    takeUntil,
} from 'rxjs';
import { FacturacionService } from '../../../services/facturacion.service';
import { AppAlertService } from '@shared/service/app/alert.service';

@Component({
    selector: 'app-asociar-venta',
    templateUrl: './asociar-venta.component.html',
    styleUrls: ['./asociar-venta.component.scss'],
})
export class AsociarVentaComponent {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    ventaForm: FormGroup;

    idCliente: number;
    clientes;
    clientesSub;
    idCobro;
    cobros;
    idFactura = this.data.codigo;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        public dialogRef: MatDialogRef<AsociarVentaComponent>,
        private facturacionService: FacturacionService,
        private alert: AppAlertService
    ) {
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    ngOnInit(): void {
        this._empresaService.getEmpresa();
        this.getClientes();
        this.newForm();
    }

    getClientes(value?) {
        let filter = value ? `?filter[razonSocial][like]=${value}` : '';
        this.facturacionService.getClientes(filter).subscribe({
            next: (res: any) => {
                this.clientes = res.result;
            },
        });
    }

    getCobros() {
        let filter = `?filter[idcliente]=${this.idCliente}`;
        this.facturacionService.getDatosCobro(filter).subscribe({
            next: (res: any) => {
                this.cobros = res.result;
                this.ventaForm.get('pago').enable();
            },
        });
    }

    newForm() {
        this.ventaForm = new FormGroup({
            cliente: new FormControl(''),
            pago: new FormControl({
                value: '',
                disabled: this.data.idcobro ? false : true,
            }),
        });

        this.initObservadores();
    }

    initObservadores() {
        this.clientesSub = this.ventaForm
            .get('cliente')
            .valueChanges.pipe(
                distinctUntilChanged(),
                debounceTime(350),
                startWith(' ')
            )
            .subscribe((value) => {
                if (typeof value == 'string') {
                    this.getClientes(value);
                } else {
                    this.idCliente = value.codigo;
                    this.getCobros();
                }
            });
    }

    displayFn(cliente): string {
        return cliente && cliente.razonSocial ? cliente.razonSocial : '';
    }

    onCloseMenu(data) {
        this.dialogRef.close(data);
    }

    onSubmit() {
        let pago = this.ventaForm.get('pago').value;

        if (pago && this.idFactura) {
            let payload = {
                idfactura: this.idFactura,
                idcobro: pago,
            };

            this.facturacionService.asociarCobro(payload).subscribe({
                next: (res: any) => {
                    if (res.success == true) {
                        this.alert.success(res.message);
                        this.onCloseMenu(true);
                    } else {
                        this.alert.error(res.message);
                    }
                },
            });
        }
    }
}
